@charset "UTF-8";
/*----------------------------------------
Transforms */
.header-mobile-bar,
.header-nav {
  background: linear-gradient(315deg, #20477D 10%, #265596 30%, #5177AB 70%);
  background-size: 100% 100vh;
  background-position: 0 0;
}

.header-nav .nav .nav-link,
.header-nav .dropdown-item {
  color: #fff;
}
.header-nav .nav .nav-link:hover, .header-nav .nav .nav-link:focus,
.header-nav .dropdown-item:hover,
.header-nav .dropdown-item:focus {
  color: #00cbed;
}
.header-nav .nav .nav-link.dropdown-toggle::after {
  color: inherit;
}

.header-mobile-bar {
  border: 0;
  padding: 1.5rem;
  height: 5rem;
}
.header-mobile-bar > * {
  -webkit-flex-basis: auto;
      -ms-flex-preferred-size: auto;
          flex-basis: auto;
}
.header-mobile-bar .header-utility-mobile {
  padding: 0;
}
.header-mobile-bar .header-utility-mobile .nav {
  padding: 0;
  margin: 0;
}

.header-global .nav-link.active,
.header-global .header-global-logo {
  display: none;
}
.header-global .dropdown-header {
  font-size: 0.875rem;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

.nav-link[data-target="#utilAccount"] {
  color: #fff !important;
  position: relative;
  text-indent: -100vw;
  overflow: hidden;
  width: 2rem;
  height: 2rem;
  border: 1px solid #fff;
  border-radius: 50%;
  background: linear-gradient(90deg, #fff 50%, transparent 0);
  background-size: 200% 100%;
  background-position: 100% 100%;
  transition: background-position 0.5s ease;
}
.nav-link[data-target="#utilAccount"]:hover, .nav-link[data-target="#utilAccount"]:focus {
  color: #5177AB !important;
  background-position: 0 100%;
  outline: 0;
}
.nav-link[data-target="#utilAccount"]::before, .nav-link[data-target="#utilAccount"]::after {
  content: "";
  position: absolute;
  border: 1px solid currentColor;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  transition: border-color 0.5s ease;
}
.nav-link[data-target="#utilAccount"]::before {
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
  top: 0.375rem;
}
.nav-link[data-target="#utilAccount"]::after {
  width: 0.75rem;
  height: 0.5rem;
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
  bottom: 0.375rem;
}

.header .navbar-toggler {
  padding: 0.5rem;
  margin: 0 -0.5rem;
  border-radius: 5px;
  outline: 0;
  transition: background-color 0.15s ease-in-out;
}
.header .navbar-toggler:hover, .header .navbar-toggler:focus {
  background-color: #5177AB;
}
.header .navbar-toggler-icon {
  width: 32px;
  height: 22px;
  padding: 10px 11px 11px 0;
  background-color: #fff;
}
.header .navbar-toggler-icon::before, .header .navbar-toggler-icon::after {
  background-color: #fff;
}
.header .navbar-toggler-open .navbar-toggler-icon {
  background-color: transparent;
}
.header .navbar-toggler-open .navbar-toggler-icon::before {
  top: 10px;
}
.header .navbar-toggler-open .navbar-toggler-icon::after {
  bottom: 11px;
}

@media (max-width: 991px) {
  .header-nav {
    top: 5rem;
    border-top: 1px solid #92B3DB;
  }
  .header-nav .dropdown-item,
.header-nav .dropdown-header,
.header-nav .nav .nav-link {
    color: #fff;
    background-color: transparent;
  }
  .header-nav .dropdown-item:focus, .header-nav .dropdown-item:hover,
.header-nav .dropdown-header:focus,
.header-nav .dropdown-header:hover,
.header-nav .nav .nav-link:focus,
.header-nav .nav .nav-link:hover {
    color: #00cbed;
  }
  .header-nav .dropdown-toggle::after {
    content: "";
  }
  .header-nav .dropdown-menu {
    font-size: 1.25rem;
    background: linear-gradient(315deg, #20477D 10%, #265596 30%, #5177AB 70%);
    background-size: 100% 100vh;
    background-position: 0 -5rem;
  }
  .header-nav .dropdown-divider {
    background-color: #92B3DB;
  }
  .header-nav .header-dropdown-back {
    font-size: inherit;
    padding-top: 1rem;
    padding-bottom: 1rem;
    border-color: #92B3DB;
  }
  .header-nav .header-dropdown-back .ico-arrow-left-sm {
    padding-right: 1rem;
  }
  .header-nav .header-dropdown-back .ico-arrow-left-sm::before {
    content: "";
  }
  .header-nav .header-primary {
    border-bottom: 1px solid #92B3DB;
    padding: 1rem 0;
    background-color: transparent;
  }
  .header-nav .header-primary .nav-link {
    font-size: 1.5rem;
    font-weight: 300;
  }
  .header-nav .header-primary .nav-link.active {
    font-weight: 500;
  }
  .header-nav .header-primary .dropdown-item.active {
    font-weight: bold;
  }
  .header-nav .header-primary .nav-link.active,
.header-nav .header-primary .dropdown-item.active {
    background-color: transparent;
    border-right-color: transparent;
  }
  .header-nav .header-primary .dropdown-toggle::after {
    line-height: 2.25rem;
  }
  .header-nav .header-global {
    padding: 0.5rem 0;
  }
  .header-nav .header-global .nav-link {
    text-transform: uppercase;
    font-weight: 700;
    letter-spacing: 0.1em;
  }

  .header-utility-dropdown {
    top: calc(5rem - 1px);
  }

  .header-collapse {
    display: block;
    height: auto !important;
    top: 5rem;
    visibility: hidden;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    transition: visibility 0.2s ease-out, -webkit-transform 0.2s ease-out;
    transition: transform 0.2s ease-out, visibility 0.2s ease-out;
    transition: transform 0.2s ease-out, visibility 0.2s ease-out, -webkit-transform 0.2s ease-out;
  }
  .header-collapse.header-nested-in {
    visibility: visible;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  .header-collapse.header-nested-out-all {
    visibility: hidden;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }

  .header-collapse-content {
    height: calc(100vh - 5rem);
  }
}
@media (min-width: 992px) {
  .header .navbar-toggler-icon {
    width: 44px;
  }

  .header-utility {
    padding: 1.5rem 3rem 1.5rem 1.5rem;
  }

  .header-utility-major {
    padding-left: 1.5rem;
  }

  .header-collapse {
    z-index: 102;
  }

  .header-global {
    padding-left: 3rem;
  }
  .header-global .navbar-toggler {
    position: relative;
    z-index: 105;
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
  }
  .header-global .nav {
    background: linear-gradient(315deg, #20477D 10%, #265596 30%, #5177AB 70%);
    display: block;
    overflow: auto;
    padding-top: 1.25rem;
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 31rem;
    z-index: 104;
    visibility: hidden;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: visibility 0.3s, -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, visibility 0.3s;
    transition: transform 0.3s ease-in-out, visibility 0.3s, -webkit-transform 0.3s ease-in-out;
  }
  .header-global .navbar-toggler-open {
    -webkit-transform: translateX(22.5rem);
            transform: translateX(22.5rem);
  }
  .header-global .navbar-toggler-open + .nav {
    visibility: visible;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  .header-global .nav-link {
    padding: 0.5rem 3rem;
    margin-right: 8rem;
    font-size: 1.5rem;
    font-weight: 300;
    line-height: 1.33333;
    border: 0;
  }
  .header-global .nav-link:hover, .header-global .nav-link:focus {
    background-color: transparent;
  }
  .header-global .nav-item:last-child {
    margin-top: 0.75rem;
    border-top: 1px solid #92B3DB;
  }
  .header-global .nav-item:last-child .dropdown-toggle {
    display: none;
  }
  .header-global .dropdown-menu {
    position: static;
    display: block;
    background-color: transparent;
    padding: 1rem 0;
    box-shadow: none;
  }
  .header-global .dropdown-menu .text-hide-overflow {
    padding: 1.5rem 3rem;
  }
  .header-global .dropdown-item {
    padding: 0.25rem 3rem;
    font-size: 1rem;
  }
  .header-global .dropdown-divider {
    margin: 1rem 0;
    background-color: #92B3DB;
  }
  .header-global .dropdown-header + .list-unstyled {
    margin-top: 0.5rem;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    gap: 1rem;
  }
  .header-global .dropdown-header + .list-unstyled .dropdown-item {
    padding: 0;
  }
  .header-global .modal-backdrop {
    z-index: 103;
  }
  .header-global .navbar-toggler:not(.navbar-toggler-open) ~ .modal-backdrop {
    pointer-events: none;
  }

  .header-primary {
    -webkit-justify-content: center;
        -ms-flex-pack: center;
            justify-content: center;
    background-color: #444;
    border-bottom: 0;
  }
  .header-primary .navbar-brand {
    margin: 0;
    padding-right: 0;
    position: absolute;
    top: 1.6875rem;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
  .header-primary .nav .nav-link {
    text-transform: uppercase;
    line-height: 1.5rem;
    font-weight: 700;
    letter-spacing: 0.1em;
    margin: 0 1.25rem;
  }
  .header-primary .dropdown-arrow {
    border-bottom-color: #265596;
  }
  .header-primary .dropdown-menu {
    background-color: #265596;
  }
  .header-primary .dropdown-item.active {
    font-weight: bold;
  }
  .header-primary .dropdown-item.active::before {
    width: 4px;
    height: 12px;
    margin: 0.3125rem 0 0 -0.5625rem;
  }
  .header-primary .dropdown-divider {
    height: 1px;
    background-color: #92B3DB;
    width: auto;
    margin: 0.75rem -1.5rem;
  }
}